.App {
  position: relative;
  background-color: #272727ff;
  width: 100vw;
  overflow-y: scroll;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.app-content {
  display: flex;
  flex-flow: column;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 100vh;
  background-color: white;
  align-items: center;
}

.header {
  width: 100%;
  height: auto;
}

/* removes unwanted bottom padding on images */
img {
  display: block
}

.tab-panel {
  width: 100%;
  box-sizing: border-box;
  padding: 2em;
  padding-top: 0;
}

.download-btn {
  padding: 2em;
}